#MainScreen_Box {

    position: relative;
    height: 100vh;
    width: 100vw;
    align-items: center;
    background-color: #FF0000;
    animation: bgColor 20s infinite linear;
  }
  @keyframes bgColor {
    12.5% {
      background-color: #ff0000;
    }
    25% {
      background-color: #ffae00;
    }
    37.5% {
      background-color: #f6ff00;
    }
    50% {
      background-color: #8bff16;
    }
    62.5% {
      background-color: #22feec;
    }
    75% {
      background-color: #002fff;
    }
    87.5% {
      background-color: #ea00ff;
    }
    100% {
      background-color: #ff0062;
    }
}
#MainScreen {
    display: flex;
    padding: 200px 200px;
    border-radius: 20px;
    flex-direction: column;
    align-items: center;
}

#nametext{
    position: relative;
    animation: Animator;
    animation-duration: 5s;
    font-size: 50px;
}

#Description{
    position: relative;
    animation: Animator;
    animation-duration: 30s;
    font-size: 20px;
}

@keyframes Animator{
    0% {opacity: 0;}
    100% {opacity: 1;}
}

