#Skill {
  font-size: 40px;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 100;
  margin-left: 40px;
}

.hidden
{
    opacity: 0;
    transform: translateX(-100%);
    transition: all 1s;
}

.show
{
    opacity: 1;
    transform: translateX(0);
}
